<template>
  <div class="home">
    <img alt="img" src="../assets/백경이(hi).png" style="width: 150px;" />
  </div>
  <div style="display: flex; flex-direction: column; height: 100vh;">
    <div style="height: 20%;">
      <header-view />
    </div>
    <div style="display: flex; flex-grow: 1;">
      <div style="width: 30%;">
        <left-view @change-view="updateMainView" />
      </div>
      <div style="flex-grow: 1;">
        <component :is="currentComponent" />
      </div>
    </div>
    <router-link to="/">돌아가기</router-link>
  </div>
</template>


<script>
import headerView from '@/components/headerView.vue';
import leftView from '@/components/leftView.vue';

export default {
  components: {
    headerView,
    leftView,
  },
  data() {
    return {
      currentComponent: null,
    };
  },
  methods: {
    async updateMainView(viewName) {
      const route = this.$router.getRoutes().find(r => r.name === viewName);
      if (route && route.components?.default) {
        // 정적 로딩일 경우
        this.currentComponent = route.components.default;
      } else if (route && typeof route.component === 'function') {
        // 동적 import된 경우
        const loaded = await route.component();
        this.currentComponent = loaded.default;
      } else {
        console.warn('해당 컴포넌트를 찾을 수 없습니다:', viewName);
      }
    },
  },
};
</script>
